.MainRecord{ 
    background: #FFF; 
  	border: 2px solid black;
  	box-sizing: border-box;
  	position: absolute;
  	height: inherit;
  	width: 100%;
}
.TextBlockRecord{
	font-family: 'Calibri Light';
	font-size: 20pt;
	text-align: center;
	margin-top: 10pt;
	margin-bottom: 50pt;
}
.RecordBlock{
	position: relative;
	display: flex;
	justify-content: space-between;
}
.ButtonMothsDay{
	background-color: #FFF;
	border-color: #C0C0C0;
	color: #4672C4; 
	font-family: 'Century Gothic';
	margin-bottom: 10pt;
}
.FontClass{
	font-family: 'Century Gothic';
	font-size: 12pt;
	font-weight: bold;
}
.RecordBlockDataTime{
	position: relative;
	display: flex;
	justify-content: space-between;
}
.ButtonDataTime{
	border-color: #FFF;
	width: 70pt;
	height: 70pt;
	border-radius: 50px;
	background-image: url(../img/VacantDay.png);
	​​​​​​​background-repeat: no-repeat;
	background-size: contain;
}
.ImgVacantDay{
	display: block;
	width: inherit;
	height: inherit;
}
.TimeRecord{
	width: 50pt;
	height: 12pt;
	display: block;
	border-color: #434342;
	font-family: 'Century Gothic';
	font-size: 12pt;
	color: #FFF; 
	text-align: bottom;
}
.CountRecord{
	border-color: #000;
	text-align: top;
}

.ButtonBlcokRecord{
	margin-top: 20pt;
	background-color: #6F389F;
	border-color: #6F389F;
	color: #FFF; 
	font-family: 'Century Gothic';
	font-size: 33pt;
}
.ButtonBlocksRecord{
	margin-right: 18%;
	margin-left: 18%;
	margin-bottom: 20pt;
	font-size: 33pt;
}